.reactSwitchCheckbox {
  height: 0;
  width: 0;
  visibility: hidden;
}

.reactSwitchLabel {
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: pointer;
  width: 100px;
  height: 50px;
  background: grey;
  border-radius: 100px;
  position: relative;
  transition: background-color .2s;
}

.reactSwitchLabel .reactSwitchButton {
  content: '';
  position: absolute;
  top: 2px;
  left: 2px;
  width: 45px;
  height: 45px;
  border-radius: 45px;
  transition: 0.2s;
  background: #fff;
  box-shadow: 0 0 2px 0 rgba(10, 10, 10, 0.29);
}

.reactSwitchCheckbox:checked + .reactSwitchLabel .reactSwitchButton {
  left: calc(100% - 2px);
  transform: translateX(-100%);
}

.reactSwitchLabel:active .reactSwitchButton {
  width: 60px;
}

.turnedOnOff{
  display: flex;
  flex-direction: row;
  height: 100px;

  margin: 5px;
  padding: 5px;

  border: #e8dd09 4px solid;
  border-radius: 7px;
  background-color: #4f4f4f;

  .turnedOn{
    margin: 5px;
    color: white;
  }

  .styleTitle{
    justify-content: center;
    color: white;
    margin: 5px;
  }
}
